.wp-block-dekode-content-generation-block {

	.components-placeholder {
		box-shadow: inset 0 0 0 1px #cc1818;
		padding: 1em;
	}

	.conversation {
		font-size: 16px;
		margin-bottom: 16px;
		width: 100%;

		& button:first-of-type {
			margin-right: 16px;
		}
	}

	.messages {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	.message {
		background: rgba(0, 124, 186, 0.1);
		font-style: italic;
		margin-bottom: 16px;
		padding: 16px;
	}
}

.wp-block-fundraising-donation-form .components-input-control__label {
	color: unset;
}
